/* Home page styles */
.home {
    display: flex;
    flex-direction: column;

    padding-top: 15%;
    height: inherit;
}

.game-title {
    margin: 0;
    margin-bottom: 5.5%;

    font-weight: 700;
    font-size: 93px;
    color: #009048;
}

.hands-container-home {
    display: flex;
    justify-content: space-around;
    align-items: flex-end;

    width: 100%;
    margin-top: auto;
    overflow: hidden;
}
